import {
    CompileReflector, ExternalReference,
    Identifiers, getUrlScheme, syntaxError
} from '@angular/compiler';
import {
    ANALYZE_FOR_ENTRY_COMPONENTS, ChangeDetectionStrategy, ChangeDetectorRef,
    Component, ComponentFactory, ComponentFactoryResolver, ComponentRef, ElementRef,
    Injector, LOCALE_ID, NgModuleFactory, NgModuleRef, QueryList, Renderer2,
    SecurityContext, TRANSLATIONS_FORMAT, TemplateRef, ViewContainerRef,
    ViewEncapsulation, CodegenComponentFactoryResolver, EMPTY_ARRAY, EMPTY_MAP,
    ReflectionCapabilities as ReflectionCapabilities, and, ccf, cmf, crt, did,
    eld, inlineInterpolate, interpolate, mod, mpd, ncd, nov, pad, pid, pod, ppd,
    prd, qud, registerModuleFactory,
    stringify as stringify, ted, unv, vid
} from '@angular/core';

export const MODULE_SUFFIX = '';
const builtinExternalReferences = createBuiltinExternalReferencesMap();

export class JitReflector implements CompileReflector {
    public guards;
    private reflectionCapabilities: ReflectionCapabilities;
    private builtinExternalReferences = new Map<ExternalReference, any>();
    constructor() { this.reflectionCapabilities = new ReflectionCapabilities(); }
    componentModuleUrl(type: any, cmpMetadata: Component): string {
        const moduleId = cmpMetadata.moduleId;

        if (typeof moduleId === 'string') {
            const scheme = getUrlScheme(moduleId);
            return scheme ? moduleId : `package:${moduleId}${MODULE_SUFFIX}`;
        } else if (moduleId !== null && moduleId !== void 0) {
            throw syntaxError(
                `moduleId should be a string in "${stringify(type)}". ` +
                `See https://goo.gl/wIDDiL for more information.\n` +
                `If you're using Webpack you should inline the template ` +
                `and the styles, see https://goo.gl/X2J8zc.`);
        }

        return `./${stringify(type)}`;
    }
    parameters(typeOrFunc: /*Type*/ any): any[][] {
        return this.reflectionCapabilities.parameters(typeOrFunc);
    }
    tryAnnotations(typeOrFunc: /*Type*/ any): any[] { return this.annotations(typeOrFunc); }
    annotations(typeOrFunc: /*Type*/ any): any[] {
        return this.reflectionCapabilities.annotations(typeOrFunc);
    }
    shallowAnnotations(typeOrFunc: /*Type*/ any): any[] {
        throw new Error('Not supported in JIT mode');
    }
    propMetadata(typeOrFunc: /*Type*/ any): {[key: string]: any[]} {
        return this.reflectionCapabilities.propMetadata(typeOrFunc);
    }
    hasLifecycleHook(type: any, lcProperty: string): boolean {
        return this.reflectionCapabilities.hasLifecycleHook(type, lcProperty);
    }
    // guards(type: any): {[key: string]: any} { return this.reflectionCapabilities.guards(type); }
    resolveExternalReference(ref: ExternalReference): any {
        return builtinExternalReferences.get(ref) || ref.runtime;
    }
}


function createBuiltinExternalReferencesMap() {
    const map = new Map<ExternalReference, any>();
    map.set(Identifiers.ANALYZE_FOR_ENTRY_COMPONENTS, ANALYZE_FOR_ENTRY_COMPONENTS);
    map.set(Identifiers.ElementRef, ElementRef);
    map.set(Identifiers.NgModuleRef, NgModuleRef);
    map.set(Identifiers.ViewContainerRef, ViewContainerRef);
    map.set(Identifiers.ChangeDetectorRef, ChangeDetectorRef);
    map.set(Identifiers.QueryList, QueryList);
    map.set(Identifiers.TemplateRef, TemplateRef);
    map.set(Identifiers.CodegenComponentFactoryResolver, CodegenComponentFactoryResolver);
    map.set(Identifiers.ComponentFactoryResolver, ComponentFactoryResolver);
    map.set(Identifiers.ComponentFactory, ComponentFactory);
    map.set(Identifiers.ComponentRef, ComponentRef);
    map.set(Identifiers.NgModuleFactory, NgModuleFactory);
    map.set(Identifiers.createModuleFactory, cmf);
    map.set(Identifiers.moduleDef, mod);
    map.set(Identifiers.moduleProviderDef, mpd);
    map.set(Identifiers.RegisterModuleFactoryFn, registerModuleFactory);
    map.set(Identifiers.Injector, Injector);
    map.set(Identifiers.ViewEncapsulation, ViewEncapsulation);
    map.set(Identifiers.ChangeDetectionStrategy, ChangeDetectionStrategy);
    map.set(Identifiers.SecurityContext, SecurityContext);
    map.set(Identifiers.LOCALE_ID, LOCALE_ID);
    map.set(Identifiers.TRANSLATIONS_FORMAT, TRANSLATIONS_FORMAT);
    map.set(Identifiers.inlineInterpolate, inlineInterpolate);
    map.set(Identifiers.interpolate, interpolate);
    map.set(Identifiers.EMPTY_ARRAY, EMPTY_ARRAY);
    map.set(Identifiers.EMPTY_MAP, EMPTY_MAP);
    map.set(Identifiers.Renderer, Renderer2);
    map.set(Identifiers.viewDef, vid);
    map.set(Identifiers.elementDef, eld);
    map.set(Identifiers.anchorDef, and);
    map.set(Identifiers.textDef, ted);
    map.set(Identifiers.directiveDef, did);
    map.set(Identifiers.providerDef, prd);
    map.set(Identifiers.queryDef, qud);
    map.set(Identifiers.pureArrayDef, pad);
    map.set(Identifiers.pureObjectDef, pod);
    map.set(Identifiers.purePipeDef, ppd);
    map.set(Identifiers.pipeDef, pid);
    map.set(Identifiers.nodeValue, nov);
    map.set(Identifiers.ngContentDef, ncd);
    map.set(Identifiers.unwrapValue, unv);
    map.set(Identifiers.createRendererType2, crt);
    map.set(Identifiers.createComponentFactory, ccf);
    return map;
}
